import actionType from "../../constant/tenant" ;
import Endpoint from '../../../utils/path/Endpoint';
import getMethod,{post,deleteMethod,putMethod} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'




function* assignMeter(action) {
    const token =  yield select(getToken) 
    try {
       // console.log("ok man ! post !!")
        const user  =action.payload ;
      //  console.log(user)
        const response = yield call(post,Endpoint.assignMeter,user,token)
        const data = yield response.data;
        const dataa = data.success ; //JSON.stringify(data.payload)
       // const dataStatus = data.httpStatus
        console.log("the response is ==== "+ JSON.stringify(dataa))
        if(JSON.stringify(dataa))
        yield put({ type:  actionType.TENANT_ASSIGN_METER_SUCCESS });
        else 
        yield put({ type:  actionType.TENANT_ASSIGN_METER_FAILURE  });
    } catch (e) {
        yield put({ type:  actionType.TENANT_ASSIGN_METER_FAILURE });
       // return;
       console.log(e)
    }    
}




export const tenantSaga = [
      takeEvery(actionType.TENANT_ASSIGN_METER, assignMeter),
      
];