import actionType from '../../constant/login'

const INITIAL_STATE ={
    isloading : false ,
    data : null,
    flag : false ,
    login : localStorage.getItem('login') ? true : false,
    user :localStorage.getItem('login') ? localStorage.getItem('my-user-x') : null ,
    userlast :localStorage.getItem('login') ? localStorage.getItem('my-user-x-lastname') : null ,
    mobile : localStorage.getItem('login') ? localStorage.getItem('my-user-x-mobile') : 0 ,
    address : localStorage.getItem('login') ? localStorage.getItem('my-user-x-address') : 0 ,
    nid : localStorage.getItem('login') ? localStorage.getItem('my-user-x-nid') : 0 ,

}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.lOG_IN:
        return {
           // ...state ,
            isLoading : true,
        
        }

        case actionType.lOG_IN_SUCCESS :
            return {
               // ...state ,
                isloading : false ,
                data : action.data[0].accessToken ,
                user : localStorage.getItem('login') ? localStorage.getItem('my-user-x') :  action.data[1].firstName,
                userlast : localStorage.getItem('login') ? localStorage.getItem('my-user-x-last') :  action.data[1].lastName,
                mobile : localStorage.getItem('login') ? localStorage.getItem('my-user-x-mobile'): action.data[1].mobileNo,
                address : localStorage.getItem('login') ? localStorage.getItem('my-user-x-address') : action.data[1].address,
                nid :  localStorage.getItem('login') ? localStorage.getItem('my-user-x-nid') : action.data[1].nidNumber,
                login : true ,
                storage : getLocalStoreData(action.data[0].accessToken,action.data[1]),
               // flag : action.data === 'success' ? true  : false ,
                               
            }

        case actionType. LOG_OUT : 
            return {
                login : false 
            }


        
        default:
            return state;  
    }

}

function getLocalStoreData(header,user) {

    localStorage.setItem('login',true)
    localStorage.setItem('header-z-my-point-new-look-my-base',header)
    localStorage.setItem('my-user-x',(user.firstName))
    localStorage.setItem('my-user-x-lastname',(user.lastName))
    localStorage.setItem('my-user-x-mobile',(user.mobileNo))
    localStorage.setItem('my-user-x-address',(user.address))
    localStorage.setItem('my-user-x-nid',(user.nidNumber))
}