import actionType from "../../constant/cpassword" ;
import Endpoint from '../../../utils/path/Endpoint';
import getMethod,{singlePutMethod} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'


function* cpassRequest(action) {
    const token =  yield select(getToken) 
    try {
        const h =action.payload ;
        console.log(h)
        const response = yield call(singlePutMethod,Endpoint.cpassword,h,token);
        const data = yield response.status ;
        console.log("is=="+JSON.stringify(response.data.status))
        if(response.data.status===400)
        yield put({ type:  actionType.CPASSWORD_REQUEST_FAILURE});
        else
        yield put({ type:  actionType.CPASSWORD_REQUEST_SUCCESS });
    } catch (e) {
        //console.log("error is = " )
        yield put({ type:  actionType.CPASSWORD_REQUEST_FAILURE });
        return;
       //console.log(e)
    }
}



export const cpasswordSaga = [
      takeEvery(actionType.CPASSWORD_REQUEST, cpassRequest)
];