import React , {Component} from 'react';
import {Container,Card,ListGroup,Accordion,Button} from 'react-bootstrap';
import styles from './Metercard.style'

class MeterCard extends Component {
    constructor(props){
        super(props)
        this.state = {
            display : 'block'
        }
    }

    print = () =>{
        this.setState({
            display : 'none'
        })
        
        const printableElements = document.getElementById('printme').innerHTML;
        const orderHtml = '<html><head><title></title></head><body>' + printableElements + '</body></html>'
    // const orderHtml =  printableElements 
        const oldPage = document.body.innerHTML;
        document.body.innerHTML = orderHtml;
        window.print();
        document.body.innerHTML = oldPage
    }

    render(){
        return(
            <div  style={{display : this.state.display}}>
            
                    <Accordion>
                    {/* <div id={this.props.printableId}> */}
                    <div id="printme2">
                    <style>
                    <style>
                    {` @media print : {
                        printme :  {
                            display: block 
                        }
                    },`}
                </style>
                        </style>
                    <Card>
                        <Card.Header style={styles.container}>
                        <Accordion.Toggle as={Button} variant="light" eventKey="0"
                        style={{...styles.text,...styles.textcolor,...styles.background,
                            backgroundColor : this.props.bgcolor,color : this.props.color}}
                        >
                        <span style={{marginLeft : 10}}>   {this.props.meter}</span>
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body>
                        <div style={{...styles.text,...styles.body,...styles.textcolorbody}}>
                                    <span>Regular Billing Day : {this.props.day}</span>
                                {/* <Button variant="link" onClick={()=>this.props.onclick(this.props.id)}>     */}
                                <span style={{color: 'red',cursor: 'pointer'}} onClick={()=>this.props.onclick(this.props.id)}>Details </span> 
                                {/* </Button> */}
                                
                            </div>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    </div>
                    {/* <Button variant="link" onClick={this.print}> print </Button> */}
                    </Accordion>
                   
            </div>
        )
    }
}

export default MeterCard ;