import React , {Component} from 'react';
import Image from 'react-bootstrap/Image'

class Desco extends Component {

    render(){
        return(
            <div>
                  <Image src={require('../../assets/images/desco.png')} style={{width : this.props.width,height:
                this.props.height }}   fluid />
  
            </div>
        )
    }
}

export default Desco 

