import React , {Component} from 'react' ;
import {Container,Row,Col,Card,Button,Form} from 'react-bootstrap';
import Label from '../../components/label/Label'
import TextField from '../../components/textfield/Textfield';
import ClickButton from '../../components/button/Button'
import styles from './Login.style';
import Desco from '../../components/desco/Desco';
import {connect} from 'react-redux' ;
import { Gradient } from 'react-gradient';
import {login} from '../../redux/Action/login';
import Loading from '../../components/loading' ;

const gradients = [
    ['#bd19d6', '#ea7d10'],
    ['#ff2121', '#25c668'],
];

class Login extends Component {
    constructor(props){
        super(props)
        this.state = {
            text : "",
            heading : "USER LOGIN",
            tailer : "METER INFO",
            username : '',
            password : '',
            gradient : [
                // ['white', '#e5aba7'],
                //  ['white', '#c5e3ef'],
                  ['white', 'white'],
              //  ['#007bff','#a23629']
             // ['#a23629','#007bff']
            ]

        }
    }

    username = (e) =>{
        let value = e.target.value ;
        this.setState({
            text : "",
            username : value 
        })
    }

    password = (e) =>{
        let value = e.target.value ;
        this.setState({
            text : "",
            password : value 
        })
    }

    makeLogin = () =>{
        let user = this.state.username ;
        let pass = this.state.password ;

        let option = {
            username : user ,
            password : pass
        }

        if(this.state.username !== '' && this.state.password !== '')
        this.props.login(option)
        else {
            this.setState({
                text :"Wrong Username or password "
            })
        }

    }

    meterInfo = () =>{
        this.setState({
            heading : "METER INFO  LOGIN",
            tailer : "USER LOGIN",
        })
    }

    userInfo = () =>{
        this.setState({
            heading : "USER LOGIN",
            tailer : "METER INFO",
        })
    }



    render(){
        console.log(this.props.flag)
        return(
            <div>
            <Gradient
                gradients={ this.state.gradient } // required
                property="background"
                duration={ 100 }
                angle="45deg"
            >
            <div  style={styles.container}>
            <div style={styles.content}>
                 {/* <Desco width ={100}/> */}
            </div>
            <div >
                    <Card style={styles.card}>
                    {
                             this.props.isloading  ?

                             <Loading show={this.props.isloading} />
                             : null 
                            }
                    
                     <Card.Body>
                     <div style={styles.text}>{this.state.text}</div> 
                     <div style={styles.heading}>{this.state.heading}</div>  
                    <Card.Img className="text-center" src={require('../../assets/images/desco.png')} style={styles.img}/> 
                   
                           <div>
                           <Label 
                           //text="User ID" 
                           text = {this.state.heading === "USER LOGIN" ? "User ID" : "BIlling Account"}
                           color = "black" /> 
                           <TextField placeholder ="User ID" onchange={(val)=>this.username(val)}  />
                           <Label text="Password" color = "black" /> 
                           <TextField placeholder ="*******" type="password" onchange={(val)=>this.password(val)} />
                           
                            <ClickButton variant="dark"
                            color = "white" width = {280}
                          //  text = "LOG IN !" 
                          text ={this.state.heading === "USER LOGIN" ? "LOG IN " : "SHOW METER INFO"}
                            onclick={this.makeLogin} />
                            </div>
                        
                     
                    </Card.Body>
                    
                    <div style={styles.tail}>
                    <ClickButton variant="link"
                            color = "white" 
                            height = {20}
                            text = {this.state.tailer}
                             
                            onclick={this.state.heading === "USER LOGIN" ? ()=>this.meterInfo() : this.userInfo } 
                            />
                    <ClickButton variant="link"
                            color = "white" 
                            text = "FORGET PASSWORD" 
                            />
                    </div>  
                    </Card>
                    
                   
            </div>
            </div>
            </Gradient>
            </div>
                
        )
        
    }
}


function mapStateToProps(state) {
	return {

        flag : state.loginReducer.flag,
        isloading : state.loginReducer.isloading
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        login : (val) =>{
             dispatch(login(val))
           }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Login)