import actionType from '../../constant/tenant'

const INITIAL_STATE ={
    isloading : false  ,
    success : false ,
    successMsg  : "" 
    

}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.TENANT_ASSIGN_METER:
        return {
           // ...state ,
            isLoading : true,
        
        }

        case actionType.TENANT_ASSIGN_METER_SUCCESS:
            return {
               // ...state,
                isloading : false ,
                success : true ,
                successMsg : "Assigned The Meter Successfully . "
            }
        case actionType.TENANT_ASSIGN_METER_FAILURE:
            return {
             //   ...state,
                isloading : false ,
                success : false 
            }
       
        
        default:
            return state;  
    }

}

function setLocalStore(billData,basicInfo){
         localStorage.setItem('meter_basic',JSON.stringify(basicInfo))
     //    localStorage.setItem('user_bill_data',JSON.stringify(billData))
}

