function mapStateToProps(state) {
	return {
        data : state.userReducer.data,
        meter : state.userReducer.meter ,
        info : state.userReducer.basicInfo,
        cross : state.cartReducer.cross,
        error : state.cartReducer.error ,
        isloading : state.userReducer.isloading,
        cartisLoading : state.cartReducer.isloading,
        cartOperation : state.cartReducer.cartOperation,
        checkoutSuccess : state.cartReducer.checkoutSuccess
	};
}
function mapDispatchToProps(dispatch) {
	return {
        getMeterList : (val) =>{
             dispatch(getMeterList(val))
           },
           userMeter : (val) => {
            dispatch(userMeter(val))
        },
        getPayableDetails : (val) =>{
             dispatch(getPayableDetails(val))
             
        },
        imageClicked : ()=>{
            dispatch(imageClicked())
        }   
	};
}
export default connect(mapStateToProps,mapDispatchToProps)(User)