import React,{Component} from 'react';
import styles from './BillingPop.style'
import {Modal,Table,Button} from 'react-bootstrap';


class PaintedComp extends Component {

    render(){
        return (

                <div style={{flexDirection : 'row' , display : 'flex',...styles.zero}}>
                    <div style={{width : this.props.width ,...styles.inside}}>  {this.props.name}  </div>
                    <div style={{width : '10%'}}> :   </div>
                    <div style={{width : '30%' ,...styles.inside,...styles.zero}}> {this.props.value}  </div>
                </div>

        )
    }
}

export default PaintedComp 