import actionType from '../../constant/cart'

const INITIAL_STATE ={
    isloading : false  ,
    id : 0 ,
    data :localStorage.getItem('cart') ? JSON.parse(localStorage.getItem('cart')) : {},
    total : localStorage.getItem('totalfototal') ? JSON.parse(localStorage.getItem('totalfototal')) : 0 ,
    cross :  JSON.parse(localStorage.getItem('totalfototal')) > 0 ? true : false ,
    checkoutSuccess : false  ,
    cartOperation : null 
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.CART:
        return {
           // ...state ,
            isLoading : true,
        
        }

        case  actionType.CART_ADD_ITEM:
        return {
           // ...state ,
            isLoading : true,
            data : cartCustomize(action.payload),
            total : cartTotal(action.payload),
            cross : true 
        }

        case actionType.CART_HIDE : 
        return {
            ...state,
            cross : false 
        }

        case actionType.CART_DELETE_ITEM : 
        return {
            ...state ,
           // console.log()
            data : deleteCartItem(action.payload),
            total : checkTotal(action.payload),
            cross : JSON.parse(localStorage.getItem('totalfototal')) > 0 ? true : false 

        }

        case actionType.CART_CHECKOUT : 
        return {
            isloading : true 
        }

        case actionType.CART_CHECKOUT_SUCCESS : 
        return {
            isloading : false ,
            checkoutMsg : "Successfully Paid ",
            cross : false ,
            data : {},
            total :  0 ,
            checkoutSuccess : true ,
            cartOperation : setCartNull()
        }

        case actionType.CART_CHECKOUT_FAILURE : 
        return {
            isloading : false ,
            checkoutMsg : " Something Wrong . Try again later  "
        }
        
        default:
            return state;  
    }

}

function setCartNull(){
    localStorage.removeItem('cart')
    localStorage.setItem('totalfototal',0)
    return  "Bill Paid Successfully..."
}

function checkCross(){
    var total = JSON.parse(localStorage.getItem('total')) ;
    let crossval = total === 0 ? false : true 
    return crossval 
}

function checkTotal(val){
    var ios = JSON.parse(localStorage.getItem('cart'));  
    let isLength = Object.keys(ios).length ;
  //  let makeZero = isLength === 0 ?  localStorage.setItem('total', 0) : JSON.parse(localStorage.getItem('total'))
    if(isLength === 0){
        localStorage.setItem('totalfototal', 0)
        return 0 ;
    }
    else {
        const value =  Object.values(val)
        let cutval  = value[1] ;
        let total =   JSON.parse(localStorage.getItem('totalfototal')) ;
       total = parseInt(total) ;
       let sum = total - cutval ;
       localStorage.setItem('totalfototal',sum)
       return sum 
    }

   // return makeZero
}
function deleteCartItem(val){
    
    const value =  Object.values(val)
    let cutval  = value[0] ;
    console.log("curt val"+cutval)
    var ios = JSON.parse(localStorage.getItem('cart'));
    ios = Object.values(ios)
    var index = cutval ;
    console.log(index);
    ios.splice(index, 1);
    ios = Object.assign({}, ios);
    localStorage.setItem('cart', JSON.stringify(ios));
    console.log(ios)
    return localStorage.getItem('cart') ? JSON.parse(localStorage.getItem('cart')) : {}
}

function cartTotal(val){
    const value =  Object.values(val)
    let cutval  = value[2] ;
    let total =  localStorage.getItem('totalfototal') ? JSON.parse(localStorage.getItem('totalfototal')) : 0;
    total = parseInt(total) ;
    let sum = cutval + total ;
    localStorage.setItem('totalfototal',sum)
    return sum ;

}

function cartCustomize(val){
     const value =  Object.values(val)

       let cart = localStorage.getItem('cart') 
                      ? JSON.parse(localStorage.getItem('cart')) : {};
        let id ;
       
        if(localStorage.getItem("id") === null) 
        {
            id = parseInt('0')
            localStorage.setItem("id", id);
        }
        else {
            id = parseInt(localStorage.getItem("id"));
            localStorage.setItem("id", ++id);
        }
        let product = {
            id : id + 1  ,
            month : value[0] ,    
            year : value[1],
            amount : value[2] ,
            billid : value[3]
        } 
                      
          cart[id] = product
          localStorage.setItem('cart', JSON.stringify(cart));
        //  this.setState({
        //      cart : localStorage.getItem('cart') 
        //      ? JSON.parse(localStorage.getItem('cart')) : {}
        //  })

        return localStorage.getItem('cart') ? JSON.parse(localStorage.getItem('cart')) : {}
     
}