import React , {Component} from 'react' ;
import {Card} from 'react-bootstrap'
import {connect} from 'react-redux' ;
import Label from '../../components/label/Label';
import TextField from '../../components/textfield/Textfield';
import ClickButton from '../../components/button/Button'
import styles from './Myaccount.style'

class MyAccount extends Component{

    handleInput = () =>{
        
    }
    render(){
        return(
            <div style={styles.container}>

               
   
                          <div style={styles.innerContainer}>
                          <Label text="First Name" color = "green" left = {10} /> 
                          <TextField value ={this.props.user}  
                        //  backcolor = '#f0ffff'
                          height = {40}
                          onchange = {this.handleInput}
                          />
                           </div>
                          
                           <div style={styles.innerContainer}>
                          <Label text="Last Name" color = "green" left = {10} /> 
                          <TextField value ={this.props.userlast}  
                        //  backcolor = '#f0ffff'
                          height = {40}
                          onchange = {this.handleInput}
                          />
                           </div>

                           <div style={styles.innerContainer}>
                          <Label text="Mobile" color = "green" left = {10} /> 
                          <TextField value ={this.props.mobile}  
                         // backcolor = '#f0ffff'
                          height = {40}
                          onchange = {this.handleInput}
                          />
                           </div>

                           <div style={styles.innerContainer}>
                          <Label text="Address" color = "green" left = {10} /> 
                          <TextField value ={this.props.address}  
                         // backcolor = '#f0ffff'
                          height = {40}
                          onchange = {this.handleInput}
                          />
                           </div>

                           <div style={styles.innerContainer}>
                          <Label text="Nid Number" color = "green" left = {10} /> 
                          <TextField value ={this.props.nid}  
                         // backcolor = 'white'
                          height = {40}
                          onchange = {this.handleInput}
                          disabled = "disabled"
                          />
                           </div>
                           <div style={{...styles.innerContainer,...styles.top}}>
                           <ClickButton 
                           variant = "success"
                           text = "UPDATE INFO"
                           //width = {350}
                           height  = {40}
                           />

                           </div>
                           
                       
                    
                   
               
            </div>

        )
    }
}

function mapStateToProps(state) {
	return {
       // courseData:state
        login : state.loginReducer.login,
        user : state.loginReducer.user ,
        userlast : state.loginReducer.userlast,
        mobile : state.loginReducer.mobile ,
        address : state.loginReducer.address , 
        nid : state.loginReducer.nid
       
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        // login : (val) =>{
        //      dispatch(login(val))
        //    }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(MyAccount)