import actionType from '../../constant/meters'

const INITIAL_STATE ={
    isloading : false  ,
    data : [],
    payable : [],
    basicInfo : [],
    error : "",
    load : null 
    

}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.METERS_GET_ALL:
        return {
            ...state ,
            isLoading : true,
        
        }

        case actionType.METERS_GET_ALL_SUCCESS :
            return {
                ...state,
                isloading : false ,
              //  data : action.data.list
              data : action.data.billing_account_list
            }
        
        case actionType.METERS_GET_PAID_BILL : 
            return {
                isloading : true 
            }
        
        case actionType.METERS_GET_PAID_BILL_SUCCESS : 
            return {
                isloading : false  ,
                payable : action.data.payload[0].payable_billing_record_list,
                basicInfo : action.data.payload[0].billing_account,
                load : setLocalStore(action.data.payload[0].payable_billing_record_list,action.data.payload[0].billing_account)
            }

        case actionType.METERS_GET_PAID_BILL_FAILURE : 
            return {
                error  : "There is some error .Please contact with administrator"
            }
        
        default:
            return state;  
    }

}

function setLocalStore(billData,basicInfo){
         localStorage.setItem('meter_basic',JSON.stringify(basicInfo))
     //    localStorage.setItem('user_bill_data',JSON.stringify(billData))
}

