import actionType from "../../constant/cart" ;
import Endpoint from '../../../utils/path/Endpoint';
import getMethod,{post} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'


function* checkout(action) {
    const token =  yield select(getToken) 
    try {
        const h =action.payload ;
        console.log("checkout=="+h)
        const response = yield call(post,Endpoint.checkout,h,token);
        const data = yield response ;
        console.log("is=="+JSON.stringify("success==="+data))
        // if(response.data.status===400)
        // yield put({ type:  actionType.CPASSWORD_REQUEST_FAILURE});
        // else
        yield put({ type:  actionType.CART_CHECKOUT_SUCCESS });
    } catch (e) {
        //console.log("error is = " )
        yield put({ type:  actionType.CART_CHECKOUT_FAILURE });
        return;
       //console.log(e)
    }
}



export const cartSaga = [
      takeEvery(actionType.CART_CHECKOUT, checkout)
];