import React , {Component} from 'react' ;
import {connect} from 'react-redux' ;
import {Card} from 'react-bootstrap';
import Label from '../../components/label/Label';
import TextField from '../../components/textfield/Textfield';
import ClickButton from '../../components/button/Button' ;
import { Gradient } from 'react-gradient';
import styles from './CPassword.style';
import {cpassRequest} from '../../../src/redux/Action/cpassword'


class CPassword extends Component {
    constructor(props){
        super(props)
        this.state = {
            password : '',
            message : '', 
            flag : false ,
            gradient : [
               
             ['#ffffff','#7a6e43']
             ]
            }
        }
        password = (e) =>{
            let value = e.target.value ;
            this.setState({
                password : value 
            })
        }

        changePassword = () =>{
         //   alert(this.state.password)
         let option = {
            
                "newPassword": this.state.password
              
         }
         if(this.state.password!== '')
         this.props.cpassRequest(option)
         else {
             this.setState({
                 flag : true ,
                 message : "Enter a password"
             },()=>console.log(this.state.flag , this.state.message))
         }
        }
    render(){
        return(
            <div style={{}}>
                <Gradient
                  gradients={ this.state.gradient } // required
                  property="background"
                  duration={ 10000 }
                  angle="100deg"
                  style={{height : '100vh'}}
                  > 
             <div style={styles.container}>
                <Card style={styles.card}>
                    {
                        this.props.flag === 1   ? 
                        <span style={styles.txtMsg}> Bad Request </span> : null 
                    }
                    
                    
                     <Card.Body>
                    <Label text="New Password" color = "black" />
                    <TextField 
                    width = {'75%'}
                    placeholder ="*******" type="password" onchange={(val)=>this.password(val)} />
                          
                    <ClickButton variant="info"
                            color = "white" 
                            width = {'75%'}
                            text = "Submit " onclick={this.changePassword} />   
                     
                    </Card.Body>
                    {
                         this.props.done ? 
                         <span style={styles.txtMsg}> {this.props.message} </span> :
                        this.state.flag ?  
                        <span style={styles.txtMsg}> {this.state.message} </span>  : 
                        <span style={styles.txtMsg}> {this.props.message} </span> 
                         
                    }
                    </Card>

                    

                    </div> 
                    </Gradient>
            </div>
        )
    }
}

function mapStateToProps(state) {
	return {
       // courseData:state
       done : state.cpasswordReducer.done ,
       message : state.cpasswordReducer.message,
       flag : state.cpasswordReducer.flag 

        
	};
}
function mapDispatchToProps(dispatch) {
	return {
		cpassRequest : (val) =>{
            dispatch(cpassRequest(val))
          }
            
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(CPassword) ;