import actionType from "../../constant/meters" ;
import Endpoint from '../../../utils/path/Endpoint';
import {getMethodById,get} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'

function* getAllMeter() {
   // const token =  yield select(getToken)
    console.log("come")
    try {
        const token =  yield select(getToken)
        const response = yield call(get,Endpoint.AllMeters,token)
        const res = yield response.data;
        const data = res.payload[0] ;
        console.log(data)
         if(res.success) 
         yield put({ type:  actionType.METERS_GET_ALL_SUCCESS, data });  
         else 
         yield put({ type:  actionType.METERS_GET_ALL_FAILURE });
            
    } catch (e) {
        console.log("the error is ="+e)
        yield put({ type:  actionType.METERS_GET_ALL_FAILURE, e });
        return;
       //
    }    
}

function* getAllPaidBill(action){
    const token =  yield select(getToken)
    try {
        let val = action.payload 
        const response = yield call(getMethodById,Endpoint.getCustomerBill,val,token)
        const data = yield response.data; 
        console.log(data)     
        yield put({ type:  actionType.METERS_GET_PAID_BILL_SUCCESS, data });    
    } catch (e) {
        yield put({ type:  actionType.METERS_GET_PAID_BILL_FAILURE, e });
        return;
    }    
}


export const meterSaga = [
      takeEvery(actionType.METERS_GET_ALL, getAllMeter),
      takeEvery(actionType.METERS_GET_PAID_BILL,getAllPaidBill)
      
];