import React,{Component} from 'react';
import styles from './Meterinfo.style'
import {Modal,Table,Button,ListGroup,Container,Row,Col,Tab} from 'react-bootstrap';
import BillButton from '../../components/button/Button'

class Meterinfo extends Component {

    constructor(props){
        super(props)
        this.state = {
            show : true 
        }
    }

    render(){
        return(
            <div style={{width : '80%'}}>
                 <Modal  show={this.state.show}  
                 aria-labelledby="contained-modal-title-vcenter" 
                 onHide={this.props.onclose}
                 class = {styles.modal}
                 >
      <Modal.Header closeButton >
        <Modal.Title id="contained-modal-title-vcenter" style={styles.title}>
         {this.props.header}
        </Modal.Title>
      </Modal.Header>
      <Modal.Header  >
        <Modal.Title id="contained-modal-title-vcenter" style={styles.body}>
        Billing Address :  {this.props.address}
        </Modal.Title>
      </Modal.Header>
      <Modal.Header  >
        <Modal.Title id="contained-modal-title-vcenter" style={styles.body}>
        Billing Day  :  {this.props.day}
        </Modal.Title>
      </Modal.Header>
      <Modal.Header  >
        <Modal.Title id="contained-modal-title-vcenter" style={styles.body}>
        Last Billing Date  :  {this.props.date}
        </Modal.Title>
      </Modal.Header>
      <Modal.Header  >
        <Modal.Title id="contained-modal-title-vcenter" style={styles.body}>
        Sanction Load  :  {this.props.sanction}
        </Modal.Title>
      </Modal.Header>
      <Modal.Header  >
        <Modal.Title id="contained-modal-title-vcenter" style={styles.body}>
        Billing Type   : <span style={styles.red}> {this.props.type} </span>
        </Modal.Title>
      </Modal.Header>
     
                            <Modal.Footer>
                            <BillButton variant="success" 
                            onclick={this.props.onclose} 
                            text = "Close"
                            />
                              
                            
                            </Modal.Footer>
                </Modal>
            </div>
        )
    }
}

export default Meterinfo ;