import React,{Component} from 'react';
import styles from './BillingPop.style'
import {Modal,Table,Button} from 'react-bootstrap';
import PaintedComp from './PaintedComp';
import CaptureImage from '../image/Image'
import NavbarCollapse from 'react-bootstrap/NavbarCollapse';


class BillingPop extends Component {

    constructor(props){
        super(props)
        this.state ={
            meter_basic : JSON.parse(localStorage.getItem('meter_basic')),
           user_bill_data : JSON.parse(localStorage.getItem('user_bill_data')),
           name : localStorage.getItem('my-user-x') +  '  ' + localStorage.getItem('my-user-x-lastname')
        }
    }
    getMonth = (d) =>{
        /*
        const monthNames = ["January", "February", "March", "April", "May", "June",
         "July", "August", "September", "October", "November", "December"
        ];

        var  date = new Date(d)
        var mm = date.getMonth() ;
        var yyyy = date.getFullYear(); 
        return  monthNames[mm]
        */
       const monthNames = d ;
        const month = monthNames.split('/')
        return month[1]
    }
    getYear = (d)=>{
        var  date = new Date(d)
        return  date.getFullYear(); 
    }
    convertDate = (d) =>{
        var  date = new Date(d)
        var dd = date.getDate(); 
        var mm = date.getMonth()+1;
        var yyyy = date.getFullYear(); 
        if(dd<10){dd='0'+dd} 
        if(mm<10){mm='0'+mm};
        return d = dd+'/'+mm+'/'+yyyy
    }

     print = () =>{
       
        
        const printableElements = document.getElementById('printme').innerHTML;
        const orderHtml = '<html><head><title></title></head><body>' + printableElements + '</body></html>'
        const oldPage = document.body.innerHTML;
        document.body.innerHTML = orderHtml;
        window.print();
        document.body.innerHTML = oldPage
    }

    render(){
       // console.log(this.state.meter_basic.sanctionedLoad)
        
        return(
            <div>
                <div >
                 <Modal   show={this.props.show}  style={{backgroundColor : 'transparent'}}
                             size="lg"
                             aria-labelledby="contained-modal-title-vcenter"
                             centered
                            >
                        
                            <Modal.Body id={this.props.printme} style={{backgroundColor : '#f8f8f8'}}>
                            <Table  bordered  style={{width : '100%',...styles.zero}}>
                            <tbody>
                                <tr style={{height : 80,background : 'transparent' , ...styles.zero}}>
                                <td style={{width : '15%' ,...styles.zero}}>
                                <CaptureImage src= {require('./desco.png')} width = {'auto'} height = {80}/>
                                </td>
                                <td style={{...styles.header,...styles.zero}}>
                                <div style={styles.identity}>Dhaka Electric Supply Company Ltd .(DESCO) </div> 
                                <div style={styles.identity}>Sales and Distribution , Dhaka </div>
                                <span style={{marginLeft : 100,...styles.identity}}>    Electric Bill -- KP  5959559 </span>
                                </td>
                                <td style={styles.zero}>
                                   <PaintedComp width={'40%'} name=  "Tarrif" value = "A"/>
                                   <PaintedComp width={'40%'} name = "Zone/Block"  value = " AE/371 "/>
                                   <PaintedComp width={'40%'} name = "Walking order"  value = " 150.00"/>
                                </td>
                                </tr>   
                            </tbody>
                            </Table>
                            <Table  bordered  style={{width : '100%',...styles.zero}}>
                                <tbody>
                                <tr style={styles.zero}>
                                
                                <td style={{width : '65%',...styles.zero}}>
                                <PaintedComp width={'40%'} name=  "Name & Address" value = {this.state.name}  />
                                
                                </td>
                                <td style={styles.zero}>
                                   <PaintedComp width={'40%'} name=  "Account No" value = {this.state.meter_basic.billAccountId}  />
                                   <PaintedComp width={'40%'} name = "Meter No"  value = {this.state.meter_basic.meterNumberOnBillSlip.substring(this.state.meter_basic.meterNumberOnBillSlip.length -10)}  />
                                   <PaintedComp width={'40%'} name = "Sanction Load"  value = {this.state.meter_basic.sanctionedLoad} /> 
                                </td>
                                </tr>
                                 </tbody>
                             </Table>
                             <Table    bordered  size="sm" responsive style={styles.zero}>
                            
                            <tbody style={styles.zero}>
                           
                                <tr style={styles.zero}>
                                    <td style={{width : '60%',background : 'transparent',...styles.zero}} > 
                                    <Table    bordered  size="sm" responsive style={styles.zero}>
                                    <tbody style={styles.zero}>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Billing Month" 
                                    value = {this.getMonth(this.state.user_bill_data.paymentWindow)}  
                                    /> 
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Billing No" 
                                    value = {this.state.user_bill_data.billingRecordId}  
                                    /> 
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <Table style={styles.zero}>
                                    <tbody style={styles.zero}>
                                    <tr>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Issue date" 
                                   // value = {this.convertDate(this.state.user_bill_data.paymentWindowStartDay)} 
                                   value = {this.state.user_bill_data.paymentWindowStartDay}
                                    /> 
                                    </td>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Issue date" 
                                   // value = {this.convertDate(this.state.user_bill_data.paymentWindowStartDay)} 
                                   value = {this.state.user_bill_data.paymentWindowEndDay}
                                    /> 
                                    </td>
                                    </tr>
                                    </tbody>
                                    </Table>
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Current " 
                                    value =  {"21/09/2019     50,345"}
                                    /> 
                                     <PaintedComp width={'40%'} name=  "Previous " 
                                    value =   {"20/08/2019 " +  '          '  +  "49,345"}
                                    />
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Difference " 
                                    value =  {"  " +  '             '  +  "    1,345"}
                                    />  
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "KWH Consumed " 
                                    value =  {"  " +  '             '  +  "    1,345"}
                                    />  
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Sub A/C Use" 
                                    value =  { " "}
                                    />  
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'40%'} name=  "Common Check Meter Use" 
                                    value =  {" "}
                                    />  
                                    </td>
                                    </tr>
                                    <tr style={{height : 225}}>
                                        <td></td>
                                    </tr>
                                    <tr >
                                     <td >
                                    <span style={styles.totalHead}> Not Less Than : </span>
                                    <span style={styles.total}> One Thousand Three Hundred Seventy Five Only </span>
                                        
                                    </td>
                                    </tr>
                                    </tbody>
                                    </Table>
                                    </td>
                                    {/* right table */}
                                    <td style={{width : '40%',background : 'transparent',...styles.zero}}>
                                        <Table    bordered  size="sm" responsive style={styles.zero}>
                                    <tbody style={styles.zero}>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'48%'} name=  "Normal KWH Charge" 
                                    value = {this.state.user_bill_data.billAmount} 
                                    /> 
                                    <PaintedComp width={'48%'} name=  "PFC Charge" 
                                    value = "0.00" 
                                    /> 
                                    <PaintedComp width={'48%'} name=  "X-former Loss" 
                                    value = "0.00" 
                                    /> 
                                    </td>
                                    </tr>

                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'48%'} name=  "Total Energy Charges" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue} 
                                    /> 
                                    <PaintedComp width={'48%'} name=  "Demand Charge" 
                                    value = "0.00" 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr style={styles.zero}>
                                    <td style={styles.zero}>
                                    <PaintedComp width={'48%'} name=  "Minimum Charge" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue} 
                                    /> 
                                    <PaintedComp width={'48%'} name=  "Service Charge" 
                                    value = "5.00" 
                                    /> 
                                     <PaintedComp width={'48%'} name=  "Suplimentary Bill" 
                                    value = {"0.00"} 
                                    /> 
                                    <PaintedComp width={'48%'} name=  "Adjustment" 
                                    value = "0.00" 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr>
                                    <td style={{fontWeight : 'bold'}}>
                                    <PaintedComp width={'48%'} name=  "Current Dues" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue + 5} 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr>
                                    <td >
                                    <PaintedComp width={'48%'} name=  "Re-print Charge" 
                                    value = {"0.00"} 
                                    /> 
                                     <PaintedComp width={'48%'} name=  "Installment of S/Drop" 
                                    value = {"0.00"} 
                                    /> 
                                     <PaintedComp width={'48%'} name=  "Meter Rent" 
                                    value = {"0.00"} 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr>
                                    <td style={{fontWeight : 'bold'}}>
                                    <PaintedComp width={'48%'} name=  "Total Dues(Rounded)" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue + 5} 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr>
                                    <td >
                                    <PaintedComp width={'48%'} name=  "VAT(On Current Dues)" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue + 5} 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr>
                                    <td style={{fontWeight : 'bold'}}>
                                    <PaintedComp width={'48%'} name=  "Total Bill" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue + 5} 
                                    /> 
                                    </td>
                                    </tr>
                                    <tr>
                                    <td >
                                    <PaintedComp width={'48%'} name=  "Total If Paid After Due Time" 
                                    value = {this.state.user_bill_data.currentBillingTimeStampUnitValue + 5} 
                                    /> 
                                    </td>
                                    </tr>

                                    </tbody>
                                    </Table>
                                        </td>
                                </tr>
                               
                            </tbody>
                            </Table>
                            </Modal.Body>
                            <Modal.Footer>
                            <Button variant="link" onClick={this.props.print}> print </Button>
                            <Button variant="success" onClick={this.props.onclose}>
                                Close
                            </Button>
                           
                            </Modal.Footer>
                             </Modal>
                             
                             </div>
                             
            </div>
        )
    }
}

export default BillingPop 