import actionType from '../../constant/user' ;

const INITIAL_STATE = {
    isloading : true ,
    ispayableloading : false ,
    data  : [],
    error :  false  ,
    payable : [],
    basicInfo : {},
    meter : '',
    user  : localStorage.getItem('user-flag-z') ? localStorage.getItem('user-my-x-this-man') : false ,
    cart : localStorage.getItem('cart') 
    ? JSON.parse(localStorage.getItem('cart')) : {}
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.GET_METER_LIST:
        return {
            ...state,
            isLoading : true,        
        }
        case actionType.GET_METER_LIST_SUCCESS :
            return {
                ...state,
                isloading : false ,
                data : action.data.billing_account_list,
                user : getStoreUSer(action.data.customer_id)
            }   
        case actionType.GET_METER_LIST_FAILURE :
            return {
                ...state ,
                isloading : false ,
                error : true ,
              // errorMessage  : 'Error Loading Data.....' 
            } 
        case actionType.GET_PAYABLE_DETAILS : 
            return {
                ispayableloading : true 
            } 
            case actionType.GET_PAYABLE_DETAILS_SUCCESS :
            return {
                ...state ,
                ispayableloading : false ,
             //   payable : action.data.payload[0].billing_record_list,
                payable : action.data.payload[0].payable_billing_record_list,
                basicInfo : action.data.payload[0].billing_account
            }
        case actionType.USER_METER : 
           return {
               ...state ,
               meter : action.payload
           }
 
            
        default:
            return state;  
    }
}

function getStoreUSer(user){
    localStorage.setItem('user-flag-z',true)
    localStorage.setItem('user-my-x-this-man',user)
}