import actionType from "../../constant/user" ;
import Endpoint from '../../../utils/path/Endpoint';
import {getMethodById,get} from '../../../utils/query/Query' ; 

import { takeEvery, call, put, select } from 'redux-saga/effects';
import { getToken } from  '../selectors'

function* getMeterList() {
    try {
        const token =  yield select(getToken)
        const response = yield call(get,Endpoint.MeterList,token)
        const res = yield response.data;
        const data = res.payload[0] ;
       // console.log(res.success,res.payload[0].customer_id,res.payload[0].billing_account_list)
     //  console.log(data.customer_id)

        if(res.success) 
        yield put({ type:  actionType.GET_METER_LIST_SUCCESS, data });  
        else 
        yield put({ type:  actionType.GET_METER_LIST_FAILURE });
            
    } catch (e) {
        console.log("the error is ="+e)
        yield put({ type:  actionType.GET_METER_LIST_FAILURE, e });
        return;
       //
    }    
}

function* getPayableDetails(action){
    const token =  yield select(getToken)
    try {
        let val = action.payload 
        const response = yield call(getMethodById,Endpoint.getCustomerBill,val,token)
        const data = yield response.data; 
        console.log(data)     
        yield put({ type:  actionType.GET_PAYABLE_DETAILS_SUCCESS, data });    
    } catch (e) {
        yield put({ type:  actionType.GET_PAYABLE_DETAILS_FAILURE, e });
        return;
    }    
}

export const userSaga = [
      takeEvery(actionType.GET_METER_LIST, getMeterList),
      takeEvery(actionType.GET_PAYABLE_DETAILS,getPayableDetails)
];