import React , {Component} from 'react';
import styles from './Metername.style';
import {Alert} from 'react-bootstrap'


class MeterName extends Component {
    constructor(props){
        super(props)

    }

    render(){

        return(
            <Alert variant="success" style={{margin: 0 }}>
             <img src="https://img.icons8.com/cotton/64/000000/severity.png" style={styles.images}></img>
              <span style={styles.meter}>  {this.props.text} </span> 
              <img style={{...styles.images,...styles.direction}} src="https://img.icons8.com/flat_round/64/000000/expand-arrow--v1.png"></img>
            
            </Alert>
        )
    }
} 

export default MeterName 