import React , {Component} from 'react';
import {getAllMeter,getAllPaidBill} from '../../../src/redux/Action/meters';
import {getMeterList,getPayableDetails,userMeter} from '../../../src/redux/Action/user';
import {connect} from 'react-redux' ;
import {Table,Container,Row,Col,Form,ListGroup} from 'react-bootstrap';
import styles from './Meters.style';
import Header from '../../components/alert/Alert' ;
import BillingPop from '../../components/billingPop/BillingPop';
import Meterinfo from '../../pages/meterinfo/Meterinfo';
import Loading from '../../components/loading' ;
import Tenant from '../../pages/tenant/Tenant' ;


class Meters extends Component {
    constructor(props){
        super(props)
        this.state = {
            text : "" ,
            tenant : false ,
            optionFlag : false ,
            monthlyFlag : false ,
            data : [],
            meterinfoFlag : false ,
            meterinfo : {}, 
            meterName : 0,
            meterId : 0 ,
            billAccountId : null ,
            tail : '&paymentStatus=paid',
            setShow : false 
        }
       
    }

    
    componentDidMount(){
        this.props.getAllMeter()
    }


    static getDerivedStateFromProps(props,state){
        if(props.data !==null && props.data!== undefined ){
        //     console.log("called..")
            return{
                data : props.data
            }
        }

        return null;
 
     }

     makeOption = (data) =>{
         localStorage.setItem('billAccountId',data.billingAccountNumberOnBillSlip)
         this.setState({
            meterinfo : data , 
            text : data.meterNumberOnBillSlip,
            optionFlag : true ,
            meterName : data.meterNumberOnBillSlip,
            billAccountId : data.billingAccountNumberOnBillSlip
         })
     }

     monthFlag = () => {
         this.setState({
            monthlyFlag : true 
         })

         let option = this.state.billAccountId + this.state.tail

         this.props.getAllPaidBill(option)
         
     }

     popupBill = (data) =>{
        localStorage.setItem('user_bill_data',JSON.stringify(data))
         this.setState({
            setShow : true 
         })
     }

     meterInfo = () =>{
        this.setState({
            meterinfoFlag : !this.state.meterinfoFlag 
        })
    }
    getYear = (d)=>{
        const data = d ;
        const year = data.split('-')
        return year[2]
    }

    tenantTrue = () =>{
        this.setState({
            tenant : true 
        })
    }

    render(){
        console.log(this.state.data)
        return(
            <div>
                <Container>
                <Row >
                    <Col  md={6}>
                    <Table  bordered hover  style={styles.top}>
                  <thead>
                    <tr style={styles.thead}>
                      <th>#</th>
                      <th>Meter</th>
                      {/* <th>BillingDay </th> */}
                      <th>Address</th>
                      <th>Type</th>
                      {/* <th>Record</th>
                      <th>Complain</th> */}
                    </tr>
                  </thead>
                  <tbody style={styles.tbody}>
                  
                  {
                        this.state.data.map((data,index)=>

                        <tr key={index} style={styles.cursor} onClick={()=>this.makeOption(data)}>
                          
                             <td>{index+1}</td>
                            <td>{data.billingAccountNumberOnBillSlip}</td>
                            {/* <td>{data.regularBillingDay}</td> */}
                            <td>{data.billAccountAddress}</td>
                            <td>{data.billingAccountType}</td> 
                            
                        </tr>
                        
                        )
                  }
                  
                  </tbody>
                  </Table>
                    </Col>
                    {/* <Col  md={1}></Col> */}
                    {
                        this.state.optionFlag ? 
                        <Col  md={3}>
                        <div style={styles.top}>
                        <Header    text = {"METER NAME :: " + this.state.meterName} width = '100%' />
                        <ListGroup >
                            <ListGroup.Item style={styles.listGroup}
                            onClick={()=>this.monthFlag()}
                            > <a style={{...styles.cursor,...styles.sentence}}> Show Paid Bill </a></ListGroup.Item>
                            <ListGroup.Item style={styles.listGroup} 
                           //  onClick={(id)=>this.goBilling('Meter-List')}
                            ><a style={{...styles.cursor,...styles.sentence}}>Add a Complain</a></ListGroup.Item>
                            <ListGroup.Item style={styles.listGroup}
                             onClick={()=>this.meterInfo()}
                            ><a style={{...styles.cursor,...styles.sentence}}>Meter Details </a></ListGroup.Item>
                            <ListGroup.Item style={styles.listGroup}
                             onClick={()=>this.tenantTrue()}
                            ><a style={styles.cursor}> ASSIGN  METER TO TENANT </a></ListGroup.Item>
                        </ListGroup>
                        </div>
                    
                    </Col>

                     : null 
                    }

                    {
                        this.state.monthlyFlag ? 
                        <Col md={3}>
                            <div style={styles.top}>
                            <Header    text = "PAID BILL MONTH :: "  width = '100%' />
                        <ListGroup >
                            {
                                this.props.payable &&  this.props.payable.length > 0  ?
                                this.props.payable.map((data,index)=>
                               
                                <ListGroup.Item style={styles.listGroup2}
                                key={index}
                                onClick={()=>this.popupBill(data)}
                                > <a style={{...styles.cursor,...styles.sentence}}> 
                                {data.paymentWindow} 
                                ,{this.getYear(data.billPaymentDueDate)}
                                </a>
                                </ListGroup.Item>
                                )

                                : null 

                            }
                           
                            
                         
                           
                        </ListGroup>
                            </div>
                        </Col>

                        : null 
                    }

                    {
                        this.state.tenant ? 
                        <Col md={3}>
                        <div style={styles.top}>
                        <Header    text = " ASSIGN TO TENANT"  width = '100%' />
                        <Tenant /> 
                        </div>
                        </Col>
                         : null 
                    }
                   
                    </Row>
                    {
                                this.state.setShow ? 
                                <BillingPop 
                                show={this.state.setShow}
                                printme = "printme"
                                print = {this.print}
                                onclose = {this.handleClose}
                                /> : null 
                            }
                
                {
             this.state.meterinfoFlag ? 
             <Meterinfo 
             meter = {this.state.meterinfo.meterNumberOnBillSlip}
             header = { this.state.text}
            // account = {this.state.meterinfo.billAccountId}
             address = {this.state.meterinfo.billAccountAddress}
             day = {this.state.meterinfo.regularBillingDay}
             date = {this.state.meterinfo.lastBillingDate}
             sanction = {this.state.meterinfo.sanctionedLoad}
             type = {this.state.meterinfo.billingAccountType}
             onclose = {()=>this.meterInfo()}
             />
             : null 
             }
             {
                                this.props.isloading  ? 
                                <Loading show={this.props.isloading} />
                                : null 
             }
                </Container>

            </div>
        )
    }
}

function mapStateToProps(state) {
	return {
        data : state.meterReducer.data,
        isloading : state.meterReducer.isloading,
        payable : state.meterReducer.payable,
        basicInfo : state.meterReducer.basicInfo
        
	};
}
function mapDispatchToProps(dispatch) {
	return {
        getAllMeter : () =>{
             dispatch(getAllMeter())
           },
        getAllPaidBill : (val) =>{
               dispatch(getAllPaidBill(val))
           }
	};
}
export default connect(mapStateToProps,mapDispatchToProps)(Meters) ;