import actionType from '../../constant/cart';

export function CartAddItem(data){
    return{
        type : actionType.CART_ADD_ITEM,
        payload : data 
    }
}

export function imageClicked(){
    return{
        type : actionType.CART_HIDE
    }
}

export function CartDeleteItem(data){
    console.log(data)
    return{
        type : actionType.CART_DELETE_ITEM,
        payload : data
    }
}

export function checkout(data){
    console.log("man !!")
    return {
        type : actionType.CART_CHECKOUT,
        payload : data
    }
}

