import React , {Component} from 'react';
import {assignMeter} from '../../../src/redux/Action/tenant';
import {connect} from 'react-redux' ;
import {Table,Container,Row,Col,Form,ListGroup} from 'react-bootstrap';
import styles from './Tenant.style';
import Header from '../../components/alert/Alert' ;
import Loading from '../../components/loading' ;
import Label from '../../components/label/Label'
import TextField from '../../components/textfield/Textfield';
import ClickButton from '../../components/button/Button'

class Tenant extends Component{
    constructor(props){
        super(props)
        this.state = {
            billAccountId : localStorage.getItem('billAccountId') ? localStorage.getItem('billAccountId') : null ,
            mobile : localStorage.getItem('my-user-x-mobile') ? localStorage.getItem('my-user-x-mobile') : 0 ,
            newUser: {
                mobileNumber : localStorage.getItem('my-user-x-mobile') ? localStorage.getItem('my-user-x-mobile') : 0,
                leasePassword : localStorage.getItem('billAccountId') ? localStorage.getItem('billAccountId') : null 
               
                  
                }  
        }
    }

    assignMeter = () =>{
        let option = {
            "billingAccountNumberOnBillSlip": this.state.billAccountId,
            "leasePassword": this.state.newUser.leasePassword,
            "mobileNumber": this.state.newUser.mobileNumber
          }

          console.log(option)
          this.props.assignMeter(option)
    }
    input = (e,name) =>{
        let value = e.target.value;
        this.setState(
          prevState => ({
            newUser: {
              ...prevState.newUser,
              [name]: value 
            }
          })
        )
      }
    render(){
     //   console.log('billAccountId=='+this.state.billAccountId)
        return(
            <div style={styles.Container}>
                <Label  text = "Billing Account Number "/>
                <TextField 
                value= {this.state.billAccountId}
                disabled = {true}
                />
                <Label  text = "Mobile Number"/>
                <TextField 
                onchange = {(e)=>this.input(e,'mobileNumber')}
                value = {this.state.newUser.mobileNumber}
                />
                <Label  text = "Password"/>
                <TextField  type="password"
                onchange = {(e)=>this.input(e,'leasePassword')}
                value = {this.state.newUser.leasePassword}
                />
                <ClickButton text="Submit" 
                onclick = {()=>this.assignMeter()}
                />
                <div>
                    {
                        this.props.success ? 
                        <div style={styles.msg}> {this.props.successMsg}</div>
                        : null 
                    }
                </div>

                     
            </div>
        )
    }
}

function mapStateToProps(state) {
	return {
        data : state.meterReducer.data,
        isloading : state.meterReducer.isloading,
        payable : state.meterReducer.payable,
        basicInfo : state.meterReducer.basicInfo,
        mobile : state.loginReducer.mobile ,
        successMsg : state.tenantReducer.successMsg,
        success : state.tenantReducer.success 
        
	};
}
function mapDispatchToProps(dispatch) {
	return {
        assignMeter : (val) =>{
             dispatch(assignMeter(val))
           },
      
	};
}
export default connect(mapStateToProps,mapDispatchToProps)(Tenant) ;