import React , {Component} from 'react' ;
import { Link } from 'react-router-dom';
import Login from '../../pages/login/Login';
import {Navbar,Nav,NavDropdown,Container,Button} from 'react-bootstrap';
import Navi from '../navigation';
import {connect} from 'react-redux' ;
import styles from './Layout.style' ;
import Desco from '../../components/desco/Desco';
import { Gradient } from 'react-gradient';
import {logout} from '../../redux/Action/login'

class Layout extends Component {
    constructor(props){
        super(props)
        this.state = {
            flag : null  ,
            gradient : [
                ['#007bff','#a23629']
                // ['#007bff', 'green'],
                //  ['red', '#e6c6a7'],
                //  ['#007bff', '#b8f54b'],
            ]
        }      
    }

    componentDidMount(){ 
        this.setState({
            flag : localStorage.getItem('login') === true ? true : false   
        })
    }

    updateState = () => {
        localStorage.setItem('value','true')
        this.setState({
            flag : true   
        })
    }

    componentDidUpdate(prevProps, prevState) {
        const check = prevProps.flag !== this.props.flag  ? this.updateState() : null
     
        }

    logout = () =>{
        localStorage.clear()
        this.props.logout()
       // this.props.history.push('/cart')
       
    }

    render(){
        console.log("üser==="+(this.props.user))
        return(
            <div >
            {
                this.props.login ?
                <div style={{margin : 0}}>  
                    <Container >
                    <Gradient
                gradients={ this.state.gradient } // required
                property="background"
                duration={ 10000 }
                angle="45deg"
            >
                    <Navbar collapseOnSelect expand="lg" style={styles.background} variant="dark">
                    <Navbar.Brand as ={Link} to="/"><Desco width = {50} /></Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto" style={styles.container}>
                        {/* <Nav.Link as={Link} to="/login" style={{color:'white'}}>Home</Nav.Link> */}
                        <Nav.Link as={Link} to="/" style={{color:'white'}}>Home</Nav.Link>
                        {/* <Nav.Link to="/">Request Meter</Nav.Link> */}
                        <Nav.Link as={Link} to="/meter" style={{color:'white'}}>All Meter</Nav.Link>
                        </Nav>
                        <Nav >
                        <Nav.Link as={Link} to="/cart">Cart 
                        <img style={styles.image} src="https://img.icons8.com/carbon-copy/100/000000/shopping-cart-promotion.png"></img>
                        </Nav.Link>
                        </Nav>
                        <NavDropdown title={<span style={{color:'white'}}>{this.props.user}</span>} style={{color:'white'}} id="collasible-nav-dropdown">
                        <Nav.Link as={Link} to="/setting" style={{color:'black'}}>Setting</Nav.Link>
                            <NavDropdown.Divider />
                            <NavDropdown.Item onClick={this.logout}>Log out</NavDropdown.Item>
                        </NavDropdown>
                    </Navbar.Collapse>
                    </Navbar>
                    </Gradient>
                    <Navi/> 
                </Container>
                    </div>: <Login/> 
            }   
            </div>
        )
    }
}

function mapStateToProps(state) {
	return {
       // courseData:state
        login : state.loginReducer.login,
        user : state.loginReducer.user 
       
       
	};
}
function mapDispatchToProps(dispatch) {
	return {
        logout : () =>{
             dispatch(logout())
           }
            
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(Layout)