import actionType from '../../constant/cpassword'

const INITIAL_STATE ={
    isloading : true ,
    data : [],
    message : null ,
    done : false ,
    flag : 0
}

export default (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case  actionType.CPASSWORD_REQUEST:
        return {
            isLoading : true,
        
        }

        case actionType.CPASSWORD_REQUEST_SUCCESS :
            return {
               // ...state ,
                isloading : false ,
                done : true ,
                message : "Successfully Password Changed",
                flag : 0
            }

        case actionType.CPASSWORD_REQUEST_FAILURE : 
            return {
                ...state ,
                done : false ,
                message : undefined ,
                flag : 1
            }

        
        default:
            return state;  
    }

}