import React , {Component} from 'react';
import {Card,Table } from 'react-bootstrap'

class CountedDiv extends Component {
    constructor(props){
        super(props)

    }

    render(){

        return(
                <div>
                        <Card style={{marginBottom : 5}}>
                            <Card.Body>
                            <Table striped bordered hover >
  
                                <tbody>
                                    <tr>
                                    <td># Meter </td>
                                    
                                    <td>6</td>
                                    </tr>
                                    <tr>
                                    <td>New Request </td>
                                    
                                    <td>1</td>
                                    </tr>
                                
                                </tbody>
                            </Table>
                                </Card.Body>
                        </Card>
                </div>
        )
    }
}

export default CountedDiv 