import React from 'react';
import { Switch, Route } from 'react-router-dom';
import User from '../../pages/user/User';
import Login from '../../pages/login/Login'
import Cart from '../../pages/cart/Cart' ;
import Setting from '../../pages/setting/Setting';
import Meters from '../../pages/meters/Meters'




const nav = () => (
  <Switch >
      <Route exact path="/" component={User} />
     <Route  path="/login" component={Login} />
     <Route  path="/meter" component={Meters} />
     <Route  path="/cart" component={Cart} />
     <Route  path="/setting" component={Setting} />
     {/* <Route path = "/meterList/:id"  component={MeterList}  /> */}
     {/* <Route  path="/reg" component={Registration} style={{backgroundCOlor:'white'}} />
     <Route path ="/billing/:id" component={Billing} />
     <Route path = "/settings"  component={Setting}  /> */}

  </Switch>
)

export default nav;