import actionType from '../../constant/user';

export function getMeterList(){
    return {
        type : actionType.GET_METER_LIST,
      //  payload : data 
    }
}

export function getPayableDetails(data){
    return {
        type : actionType.GET_PAYABLE_DETAILS,
        payload  : data 
    }
}

export function userMeter(data){
    return {
        type : actionType.USER_METER,
        payload  : data 
    }
}